<template>
    <div class="user-control">
        <div class="button-item">
            <span class="txt">
                <i class="icon bbx-font bbx-icon-lang"></i>
                <el-dropdown class="txt-right lang-list" placement="bottom" trigger="click" @command="changeLang">
                    <span>{{ langList.hasOwnProperty(lang) ? langList[lang].name : $L('切换语言') }}<i class="el-icon-arrow-down el-icon--right"></i></span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item v-for="(item,index) in langList" :key="index" :command="index">{{ item.name }}</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </span>
        </div>
        <template v-if="openCart == 1">
            <div class="button-item" v-if="openSign===1">
            <span class="txt" @click="doSign">
                <i class="icon bbx-font bbx-icon-yuyuehuodong"></i>
                <span class="txt-right">{{ isSign || isSignLater ? $L('今天已签到') : $L('签到领积分') }}</span>
            </span>
            </div>
            <div class="button-item" v-if="openPhone>0">
            <span class="txt" @click="bandPhone()">
                <i class="icon bbx-font bbx-icon-bangdingshouji"></i>
                <span class="txt-right" v-if="openPhone==1">{{ phone!=='' ? phone : $L('绑定手机') }}</span>
                <span class="txt-right" v-if="openPhone==2">{{ phone!=='' ? phone : $L('绑定手机') }}</span>
            </span>
            </div>
            <div class="button-item">
            <span class="txt" @click="showShopCart">
                <i class="icon bbx-font bbx-icon-gouwuche2"></i>
                <span class="txt-right">{{$L('购物车')}}</span>
                <span class="num-txt">{{ cartNum }}</span>
            </span>
            </div>
        </template>
        <div class="button-item">
            <span class="txt" @click="goto('accountInfo')">{{ $L('欢迎！') + userName }}</span>
        </div>
        <div class="button-item">
            <div class="user-img-box" @click="goto('accountInfo')">
                <user-avatars :src="userImg"></user-avatars>
            </div>
        </div>
        <div class="button-item">
            <span class="txt" @click="logout">{{$L('退出')}}</span>
        </div>
    </div>
</template>

<script>
    import shopCart from "../../../../../views/web/shopCart"
    import UserAvatars from "../../../../common/userAvatars";

    export default {
        name: "userCenter",
        components: {UserAvatars},
        inject: ["cleanHeartbeat","reload"],
        data() {
            return {
                isSignLater: false,
                langList:this.$C('langList', 'lang'),
                lang:this.$lang()
            }
        },
        computed: {
            cartNum: {
                get() {
                    return this.$store.state.heartbeatData.hasOwnProperty('cartNum') ? this.$store.state.heartbeatData.cartNum : 0;
                }
            },
            isSign: {
                get() {
                    if (this.loginConfig
                        && this.loginConfig.hasOwnProperty('settings')
                        && this.loginConfig.settings.hasOwnProperty('isSign')) {
                        return this.loginConfig.settings.isSign;
                    } else {
                        return 0;
                    }
                }
            },
            openCart: {
                get() {
                    if (this.loginConfig
                        && this.loginConfig.hasOwnProperty('settings')
                        && this.loginConfig.settings.hasOwnProperty('openCart')) {
                        return this.loginConfig.settings.openCart;
                    } else {
                        return 0;
                    }
                }
            },
            openSign: {
                get() {
                    if (this.loginConfig
                        && this.loginConfig.hasOwnProperty('settings')
                        && this.loginConfig.settings.hasOwnProperty('openSign')) {
                        return this.loginConfig.settings.openSign;
                    } else {
                        return 0;
                    }
                }
            },
            openPhone: {
                get() {
                    if (this.loginConfig
                        && this.loginConfig.hasOwnProperty('settings')
                        && this.loginConfig.settings.hasOwnProperty('openPhone')) {
                        return this.loginConfig.settings.openPhone;
                    } else {
                        return 0;
                    }
                }
            },
            phone: {
                get() {
                    if (this.loginConfig
                        && this.loginConfig.hasOwnProperty('userInfo')
                        && this.loginConfig.userInfo.hasOwnProperty('phone')) {
                        return this.loginConfig.userInfo.phone;
                    } else {
                        return null;
                    }
                }
            },
            userName: {
                get() {
                    if (this.loginConfig
                        && this.loginConfig.hasOwnProperty('userInfo')
                        && this.loginConfig.userInfo.hasOwnProperty('nickname')) {
                        return this.loginConfig.userInfo.nickname;
                    } else {
                        return '';
                    }
                }
            },
            userImg: {
                get() {
                    if (this.loginConfig
                        && this.loginConfig.hasOwnProperty('userInfo')
                        && this.loginConfig.userInfo.hasOwnProperty('userImg')) {
                        return this.loginConfig.userInfo.userImg;
                    } else {
                        return '';
                    }
                }
            },
            loginConfig: {
                get() {
                    return this.$store.state.loginConfig;
                }
            }
        },
        methods: {
            changeLang(id){
                if (id == 'lang_0') {
                    this.$i18n.locale = 'zh';
                } else if (id == 'lang_1'){
                    this.$i18n.locale = 'tw';
                } else if (id == 'lang_2') {
                    this.$i18n.locale = 'en';
                }
                this.$lang(id);
                this.reload();
            },
            doSign() {
                const _self = this;
                _self.$api("doSign", {}, function (reData) {
                    if (reData && reData.flag === 1) {
                        _self.$message.success(_self.$L('签到成功！获得{$1}积分', [reData.data.score]));
                        _self.isSignLater = true;
                    } else if (reData && reData.flag === 2) {
                        _self.$message.success(_self.$L('今天已签到！'));
                        _self.isSignLater = true;
                    } else {
                        _self.$message.error(_self.$L('签到失败！请稍后重试'));
                    }
                }, function (error) {
                    console.log(error);
                    _self.$message.error(_self.$L('签到失败！请稍后重试'));
                });
            },
            bandPhone() {
                if (this.openPhone === 1) {
                    this.$router.push({name: 'accountSafety'});
                }
                if (this.openPhone === 2) {
                    this.$router.push({name: 'accountInfo'});
                }
            },
            showShopCart() {
                this.$store.commit('setDialogObj', {
                    name: this.$L('购物车'),
                    view: shopCart,
                    width: '900px',
                    loading: true
                });
            },
            logout() {
                const _self = this;
                _self.$confirm(_self.$L('确认退出登录吗？'), _self.$L('退出登录'), {
                    confirmButtonText: _self.$L('确定'),
                    cancelButtonText: _self.$L('取消'),
                    type: 'warning',
                    distinguishCancelAndClose: true
                }).then(() => {
                    _self.cleanHeartbeat();
                    _self.$router.push({name:'logout'});
                }).catch(action => {

                });
            },
            goto(routerName) {
                this.$router.push({name: routerName});
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../../../../assets/css/common/lib.scss';

    .user-control {
        position: absolute;
        top: 0;
        right: 0;
        color: #fff;
        height: 100%;
        line-height: inherit;
        font-size: inherit;

        .button-item {
            position: relative;
            float: left;
            line-height: inherit;
            height: 100%;
            padding: 0 10px;

            .txt {
                cursor: pointer;
                @include diy-animation-show(all);

                &:hover {
                    opacity: 0.8;
                }

                .icon {
                    font-size: inherit;
                }

                .txt-right {
                    margin-left: 5px;
                }

                .num-txt {
                    margin-left: 5px;
                    line-height: 40px;
                }

                .lang-list{
                    font-size: inherit;
                    color: inherit;
                }
            }

            .user-img-box {
                position: relative;
                width: 30px;
                height: 30px;
                overflow: hidden;
                margin-top: 10px;
                border-radius: 30px;
                border: 1px solid #FFFFFF;
                cursor: pointer;
                box-sizing: unset;

                .user-img {
                    width: 100%;
                    height: 100%;
                    border-radius: 50%;
                    .img-error{
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        }
    }
</style>
