<template>
    <div class="top-view mainBgColor">
        <div class="top-left" :style="darkenThemeColor" @click="goHome" :title="$L('返回网站首页')">
            <el-image class="logo" :src="logo" fit="contain">
                <div slot="error">
                    <span class="bbx-font bbx-icon-shouye"></span>
                </div>
            </el-image>
        </div>
        <div class="top-right">
            <user-control/>
        </div>
    </div>
</template>

<script>
import userControl from './topViewChildren/userControl.vue'
    export default {
        name: "topView",
        components: {
            userControl
		},
        computed: {
            themeColor: {
                get() {
                    return this.$themeColor();
                }
            },
            darkenThemeColor: {
                get() {
                    return 'background-color:#' + this.$lightenDarkenColor(this.themeColor, -30) + ';';
                }
            },
            logo:{
                get(){
                    return this.$C('webLogo');
                }
            }
        },
        methods:{
            goHome(){
                this.$gotoWebHome();
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../../../assets/css/common/lib.scss';

    .top-view {
        position: absolute;
        width: 100%;
        height: 50px;
        top: 0;
        left: 0;
        z-index: 100;
        line-height: 50px;
        @extend .text-one-line;
        @extend .no-select;

        .top-left {
            position: absolute;
            width: 200px;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 100;
            line-height: inherit;
            text-align: left;
            color: #FFFFFF;
            font-size: 20px;
            padding: 0 20px 0 45px;

            .logo{
                position: absolute;
                @include diy-animation-show(all);
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
                text-align: center;
                font-size: 20px;
                cursor: pointer;
                &:hover{
                    opacity: 0.8;
                }
                .bbx-font{
                    font-size: 26px;
                }
            }
        }
        .top-right{
            position: absolute;
            width: calc(100% - 200px);
            height: 100%;
            right: 0;
            top: 0;
            z-index: 90;
            line-height: inherit;
            font-size: 14px;
        }
    }
</style>