<template>
    <div class="shop-cart" v-loading="loading">
        <iframe :src="url" class="windows" style="height:100%;" ref="ifr" @load="onLoad"></iframe>
    </div>
</template>

<script>
    export default {
        name: "shopCart",
        props: {
            height: {
                type: String,
                default: '',
            },
            loading: {
                type: Boolean,
                default: false
            }
        },
        created(){
            if(this.$isMobile()){
                this.$router.push({name:"cart"});
            }
        },
        computed: {
            url: {
                get() {
                    return this.$C('serverUrlAjax') + "/exusers/u_cart.php?idweb=" + this.$getWebId() + "&act=show&lang=" + this.$langID() + "&v=9";
                }
            }
        },
        methods: {
            onLoad() {
                this.$refs.ifr.contentWindow.postMessage({msg: "isVue"}, this.$C('serverUrlAjax'));
            }
        },
    }
</script>

<style lang="scss" scoped>
    @import '../../assets/css/common/lib.scss';

    .shop-cart {
        position: relative;
        width: 100%;
        padding: 20px;
        height: 500px;
        .windows {
            position: relative;
            width: 100%;
        }
    }
</style>