<template>
    <div class="left-view">
        <div class="left-title">
            <span>{{ $L('会员中心') }}</span>
        </div>
        <div class="left-menu">
            <vue-scroll>
                <template v-for="item in newMenuList">
                    <div class="menu-item"
                        :class="{active:item.hasOwnProperty('showRouter') && $in_array($route.name,item.showRouter)}"
                        @click="goto(item)" :key="item.id">
                        <div class="active-left mainBgColor"></div>
                        <div class="active-right"></div>
                        <span class="bbx-font" :class="item.icon"></span>
                        <span>{{$L(item.name)}}</span>
                    </div>
                </template>
            </vue-scroll>
        </div>
    </div>
</template>

<script>
    export default {
        name: "leftView",
        data() {
            return {
                menuList: this.$C('menuList'),
                newMenuList: [],
            }
        },
        computed:{
            menuClose:{
                get(){
                    if(this.$store.state.loginConfig.hasOwnProperty('settings') && this.$store.state.loginConfig.settings.hasOwnProperty('menu') && this.$store.state.loginConfig.settings.menu.hasOwnProperty('close')){
                        return this.$store.state.loginConfig.settings.menu.close;
                    }else{
                        return [];
                    }
                }
            },
            menuReName:{
                get(){
                    if(this.$store.state.loginConfig.settings.hasOwnProperty('menu') && this.$store.state.loginConfig.settings.menu.hasOwnProperty('reNameNew')){
                        return this.$store.state.loginConfig.settings.menu.reNameNew;
                    }else{
                        return {};
                    }
                }
            }
        },
        created(){
            const _self = this;
            for(let item of _self.menuList){
                if(!item.hideWeb && !_self.$in_array(item.id,_self.menuClose)){
                    if(_self.$lang()==="lang_0" || _self.$lang()==="lang_1"){
                        if(_self.menuReName.hasOwnProperty(item.id)){
                            item.sort = _self.menuReName[item.id].sort;
                            item.name = _self.menuReName[item.id].name;
                        }else{
                            item.sort = item.id=="systemSettings"?9999:0;
                        }
                    }else{
                        item.sort = item.id=="systemSettings"?9999:0;
                    }
                    //积分商城与我的积分排序级别一致
                    //if(item.id=="integralShop"&&_self.menuReName.hasOwnProperty('myPoint'))
                    //    item.sort = _self.menuReName['myPoint'].sort;
                    _self.newMenuList.push(item);
                }
            }
            _self.newMenuList = _self.newMenuList.sort((a, b) => {
                if(a.sort - b.sort==0){
                    return a.oldSort - b.oldSort
                }
                return a.sort - b.sort
            });
        },
        methods: {
            // checkMenuName(item){
            //     if(this.$lang()==="lang_0" || this.$lang()==="lang_1"){
            //         if(this.menuReName.hasOwnProperty(item.id)){
            //             return this.menuReName[item.id].name;
            //         }else{
            //             return this.$L(item.name);
            //         }
            //     }else{
            //         return this.$L(item.name);
            //     }
            // },
            goto(item) {
                if (item.hasOwnProperty('router') && item.router !== '') {
                    let params = {};
                    if (item.hasOwnProperty('params')) {
                        Object.assign(params, item.params);
                    }
                    if (item.hasOwnProperty('paramsData')) {
                        Object.assign(params, item.paramsData);
                    }
                    this.$router.push({name: item.router, params: params});
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../../../assets/css/common/lib.scss';

    .left-view {
        position: absolute;
        width: 200px;
        height: calc(100% - 50px);
        top: 50px;
        left: 0;
        background: #353942;
        z-index: 90;
        @extend .no-select;

        .left-title {
            position: absolute;
            width: 100%;
            height: 50px;
            top: 0;
            left: 0;
            z-index: 100;
            color: #FFFFFF;
            font-size: 18px;
            line-height: 50px;
            padding-left: 20px;
            border-bottom: 1px solid #46494e;
        }

        .left-menu {
            position: absolute;
            width: 100%;
            height: calc(100% - 50px);
            top: 50px;
            left: 0;
            z-index: 100;

            .menu-item {
                position: relative;
                width: 100%;
                height: 40px;
                line-height: 40px;
                color: #FFFFFF;
                font-size: 14px;
                padding-left: 20px;
                cursor: pointer;
                @include diy-animation-show(all);

                .active-left{
                    position: absolute;
                    width: 3px;
                    height: 100%;
                    left: 0;
                    top: 0;
                    z-index: 100;
                    opacity: 0;
                }

                .active-right{
                    position: absolute;
                    right: 0;
                    top: 50%;
                    z-index: 100;
                    width: 0;
                    height: 0;
                    border-top: 8px solid transparent;
                    border-right: 12px solid #eff1f5;
                    border-bottom: 8px solid transparent;
                    opacity: 0;
                    transform: translateY(-50%);
                }

                .bbx-font {
                    margin-right: 10px;
                    font-size: 16px;
                }

                &.active {
                    background-color: #222429;

                    .active-left{
                        opacity: 1;
                    }
                    .active-right{
                        opacity: 1;
                    }
                }

                &:hover {
                    background-color: #52555f;
                }
            }
        }
    }
</style>